import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)
function loadView (path) {
  return resolve => require(['@/components/' + path], resolve)
}
export default new Router({
  routes: [
    {
      path: '/',
      name: '',
      component: loadView('Index')
    },
    {
      path: '/game',
      name: 'game',
      component: loadView('game/Index')
    },
    {
      path: '/gamePanel',
      name: 'gamePanel',
      component: loadView('game/GamePanel'),
      children: [
        {
          path: '/mineSweeping',
          name: 'mineSweeping',
          component: loadView('game/mineSweeping/Index')
        }
      ]
    }
  ]
})



